<template>
  <div>
    <NavbarNotLoggedIn />
    <div class="min-h-[85vh] h-full">
      <div class="grid grid-cols-6">
        <div class="col-span-2"></div>
        <div v-if="dataCart == ''" class="col-span-2 mt-10"><p class="text-4xl">Cart Is Empty</p></div>
        <div v-else class="col-span-2 mt-10">
          <p class="text-2xl font-semibold">
            Total Price: <span class="italic">{{ total }}৳</span>
          </p>

          <div class="grid grid-cols-6 border border-yellow-700 m-2 rounded-lg" v-for="(d, i) in dataCart" :key="i">
            <div class="col-span-5 flex">
              <img :src="d.pic" alt="pic" class="w-[250px] m-2 rounded" />
              <div class="m-10 text-left">
                <p class="text-2xl font-bold">{{ d.tag }}</p>
                <p class="text-lg">Weight: {{ d.weight }}kg</p>
                <p class="text-lg text-green-700">
                  Price: <span class="font-semibold">{{ d.sellingPrice }}৳</span>
                </p>
              </div>
            </div>
            <div class="col-span-1 flex items-center justify-center">
              <p @click="Remove(d.id)"><i class="fa-solid fa-xmark text-red-500 text-2xl hover:scale-110 hover:cursor-pointer"></i></p>
            </div>
          </div>
          <button @click="CheckOut()" class="bg-yellow-500 py-2 px-4 text-xl text-gray-100 hover:text-black hover:bg-yellow-400 hover:scale-105 float-right">Checkout <i class="fa-solid fa-arrow-right"></i></button>
        </div>
        <div class="col-span-2"></div>
      </div>
    </div>
  </div>
</template>
<script>
import NavbarNotLoggedIn from '@/components/NavbarNotLoggedIn.vue';
import api from '../../boot/axios.js';
export default {
  components: { NavbarNotLoggedIn },
  data() {
    return {
      dataCart: [],
      total: 0,
    };
  },
  created() {
    this.getData();
  },

  methods: {
    async getData() {
      this.total = 0;
      this.dataCart = this.$store.getters.cartItems;
      console.table(this.dataCart);
      for (let i = 0; i < this.dataCart.length; i++) {
        this.total += this.dataCart[i].sellingPrice;
      }
    },
    Remove(i) {
      console.log(true);
      this.$store.commit('removeFromCart', i);
      this.getData();
    },
    CheckOut() {
      this.$router.push('/checkout');
    },
  },
};
</script>
