<template>
  <div class="bg-gray-100">
    <NavbarNotLoggedIn />
    <div class="min-h-[90vh] h-full">
      <div class="grid grid-cols-7">
        <div class="col-span-2"></div>
        <div class="col-span-3">
          <p class="text-4xl my-5 underline">Accont</p>

          <div class="bg-white py-1 my-3 rounded shadow" v-for="d in data" :key="d">
            <div class="grid grid-cols-2">
              <div class="grid-cols-1 text-left ml-10"><p class="text-2xl">Order Status:</p></div>
              <div class="grid-cols-1 text-right mr-10">
                <p class="text-3xl text-cusYellow font-bold">{{ d.status }}</p>
              </div>

              <div class="grid-cols-1 text-left ml-10">
                <p class="text-xl">Order Placed</p>
              </div>
              <div class="grid-cols-1 text-right mr-10">
                <p class="text-xl">{{ d.created_at.substring(0, 10) }}</p>
              </div>

              <div class="grid-cols-1 text-left ml-10">
                <p class="text-base">Sub total</p>
              </div>
              <div class="grid-cols-1 text-right mr-10">
                <p class="text-base">{{ d.total2 }} ৳</p>
              </div>
              <div class="grid-cols-1 text-left ml-10">
                <p class="text-base">Vat</p>
              </div>
              <div class="grid-cols-1 text-right mr-10">
                <p class="text-base">{{ d.vatAmount }} ৳</p>
              </div>
              <div class="grid-cols-1 text-left ml-10">
                <p class="text-base">Delivery Charge</p>
              </div>
              <div class="grid-cols-1 text-right mr-10">
                <p class="text-base">{{ d.deliveryCharge }} ৳</p>
              </div>

              <div class="grid-cols-1 text-left ml-10">
                <p class="text-lg font-semibold">Total</p>
              </div>
              <div class="grid-cols-1 text-right mr-10">
                <p class="text-lg font-semibold">{{ d.total3 }} ৳</p>
              </div>

              <div class="bg-gray-200 m-1 rounded col-span-2" v-for="c in d.product" :key="c">
                <div class="grid grid-cols-5">
                  <div class="col-span-1"><img :src="c.image" class="p-3 rounded-lg" alt="" /></div>
                  <div class="col-span-2 text-left px-5 py-2">
                    <p class="text-lg">{{ c.name }}</p>
                    <p class="">Weight: {{ c.weight }} kg</p>
                  </div>
                  <div class="col-span-2 text-left px-5 py-2">
                    <p class="">Price: {{ c.price }} ৳</p>
                    <p class="">Quantity: {{ c.unit }}</p>
                    <p class="font-semibold">Total: {{ c.price * c.unit }} ৳</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="col-span-2"></div>
      </div>
    </div>
  </div>
</template>
<script>
import NavbarNotLoggedIn from '@/components/NavbarNotLoggedIn.vue';
import api from '../../boot/axios.js';
export default {
  components: { NavbarNotLoggedIn },
  data() {
    return {
      id: localStorage.getItem('user'),
      data: [],
    };
  },
  created() {
    this.getData();
  },

  methods: {
    async getData() {
      console.log(this.id);
      if (this.id == undefined) {
        alert('Please Log in');
        this.$router.push({ name: 'login' });
      }
      await api
        .post(`/api/order_view/`, { a: this.id, b: true })
        .then((res) => {
          this.data = res.data;
          console.table(this.data);

          for (let i = 0; i < this.data.length; i++) {
            let a = 0;
            for (let j = 0; j < this.data[i].product.length; j++) {
              a += parseFloat(this.data[i].product[j].price);
            }
            console.log(a);
            this.data[i].total2 = a;
            this.data[i].total3 = a + parseFloat(this.data[i].deliveryCharge) + parseFloat(this.data[i].vatAmount);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
