<template>
  <div>
    <NavbarNotLoggedIn />
    <div class="min-h-[90vh] h-full">
      <div class="grid grid-cols-6">
        <div class="col-span-2"></div>
        <div class="col-span-2 mt-10 mx-10">
          <div class="w-full">
            <p class="text-2xl text-left" @click="DeliveryCheck()">
              Delivery To<span @click="modalAddress = true" class="float-right hover:text-red-600 cursor-pointer hover:scale-105 mr-2"><i class="fa-solid fa-plus"></i></span>
            </p>
          </div>
          <select v-model="address" @change="DeliveryCheck()" class="w-full rounded">
            <option value="" hidden>Choose Address</option>
            <option v-for="d in dataAdress" :key="d.id" :value="d.id">
              <p class="text-xl">{{ d.type }}, {{ d.address }}</p>
            </option>
          </select>
          <br /><br />
          <p class="text-2xl text-left">Delivery Method</p>
          <div class="grid grid-cols-2 mt-1">
            <div class="col-span-1 flex">
              <label class="inline-flex items-center">
                <input type="radio" class="form-radio h-5 w-5 text-yellow-500" name="radio1" value="option1" v-model="selectedOption1" />
                <span class="ml-2 text-gray-700">Pick Up from Farm</span>
              </label>
            </div>
            <div class="col-span-1 flex mt-1">
              <label class="inline-flex items-center ml-6">
                <input type="radio" class="form-radio h-5 w-5 text-yellow-500" name="radio1" value="option2" v-model="selectedOption1" />
                <span class="ml-2 text-gray-700">Home Delivery</span>
              </label>
            </div>
          </div>

          <p class="text-2xl text-left mt-5">Payment Method</p>
          <div class="grid grid-cols-2 mt-1">
            <div class="col-span-1 flex">
              <label class="inline-flex items-center">
                <input type="radio" class="form-radio h-5 w-5 text-yellow-500" name="radio2" value="option1" v-model="selectedOption2" />
                <span class="ml-2 text-gray-700">Online Payment</span>
              </label>
            </div>
            <div class="col-span-1 flex mt-1">
              <label class="inline-flex items-center ml-6">
                <input type="radio" class="form-radio h-5 w-5 text-yellow-500" name="radio2" value="option2" v-model="selectedOption2" />
                <span class="ml-2 text-gray-700">Cash on Delivery</span>
              </label>
            </div>
          </div>

          <p class="text-2xl text-left mt-5">Feedback</p>
          <div class="mt-1">
            <textarea v-model="feedback" class="min-w-full bg-gray-100 h-28" placeholder="Please Share Your Experience"></textarea>
          </div>

          <p class="text-2xl text-left mt-5">Bill Details</p>
          <div class="mt-1 bg-gray-100 grid grid-cols-2 p-4 rounded-lg">
            <div class="col-span-1">
              <p class="text-left text-xl">Sub Total</p>
            </div>
            <div class="col-span-1">
              <p class="text-right text-xl">
                <span class="font-bold">৳</span>
                {{ total }}
              </p>
            </div>

            <div class="col-span-1">
              <p class="text-left text-xl">Delivery Charge</p>
            </div>
            <div class="col-span-1">
              <p class="text-right text-xl">
                <span class="font-bold">৳</span>
                {{ deliveryCharge }}
              </p>
            </div>
            <div class="col-span-2 border-b border-black"></div>
            <div class="col-span-1">
              <p class="text-left text-xl font-bold">Total</p>
            </div>
            <div class="col-span-1">
              <p class="text-right text-xl font-bold">
                <span class="font-bold">৳</span>
                {{ deliveryCharge + total }}
              </p>
            </div>
          </div>
          <div class="mt-5 mr-4"><button @click="Order()" class="bg-cusYellow text-black py-1 px-8 text-lg rounded-2xl float-right hover:scale-105">Place Order</button></div>
        </div>
        <div class="col-span-2"></div>
      </div>
    </div>

    <div v-if="modalAddress" class="opacity-80 fixed inset-0 z-40 bg-black"></div>
    <div v-if="modalAddress" class="fixed inset-0 z-40 flex justify-center items-center">
      <div class="w-2/6 fixed bg-white rounded z-50">
        <div class="">
          <p class="text-2xl font-bold p-2 font-mono bg-gray-100 mb-2">Address Add</p>

          <div class="px-10">
            <p class="text-left font-semibold">Address Type</p>
            <div class="grid grid-cols-3 space-x-2 rounded bg-gray-100 p-2" x-data="app">
              <div @click="areaNameVar = 'home'">
                <input type="radio" name="option" id="1" class="peer hidden" checked />
                <label for="1" class="block cursor-pointer select-none rounded-xl p-1 text-center peer-checked:bg-cyan-500 peer-checked:font-bold peer-checked:text-white text-lg">Home</label>
              </div>

              <div @click="areaNameVar = 'office'">
                <input type="radio" name="option" id="2" class="peer hidden" />
                <label for="2" class="block cursor-pointer select-none rounded-xl p-1 text-center peer-checked:bg-cyan-500 peer-checked:font-bold peer-checked:text-white text-lg">Office</label>
              </div>

              <div @click="areaNameVar = 'other'">
                <input type="radio" name="option" id="4" class="peer hidden" />
                <label for="4" class="block cursor-pointer select-none rounded-xl p-1 text-center peer-checked:bg-cyan-500 peer-checked:font-bold peer-checked:text-white text-lg">Other</label>
              </div>
            </div>
          </div>

          <div class="px-10">
            <p class="text-left font-semibold">Address</p>
            <textarea v-model="addressVarr" class="rounded bg-gray-50 w-full"></textarea>
          </div>

          <div class="grid grid-cols-2">
            <div class="pl-10 pr-2">
              <p class="text-left font-semibold">Disctrict</p>
              <input v-model="district" class="rounded bg-gray-50 w-full border border-black py-1 px-3" placeholder="Disctrict" />
            </div>
            <div class="pr-10 pl-2">
              <p class="text-left font-semibold">Thana</p>
              <input v-model="thana" class="rounded bg-gray-50 w-full border border-black py-1 px-3" placeholder="Thana" />
            </div>

            <div class="pl-10 pr-2">
              <p class="text-left font-semibold">Area</p>
              <select v-model="areaVarr" class="rounded bg-gray-50 w-full border border-black py-1 px-3">
                <option value="" hidden>Select Area</option>
                <option v-for="d in dataArea" :key="d" :value="d.id">{{ d.name }}</option>
              </select>
            </div>
            <div class="pr-10 pl-2">
              <p class="text-left font-semibold">Zip Code</p>
              <input v-model="zip" class="rounded bg-gray-50 w-full border border-black py-1 px-3" placeholder="Zip Cde" />
            </div>
          </div>
        </div>

        <!-- Modal content here -->

        <div class="flex justify-center m-2 mb-4">
          <button @click="AddressCreate()" class="bg-button2 hover:bg-button hover:text-black text-white font-bold text-base py-2 px-4 mx-1 mt-2 rounded">Add</button>
          <button
            @click="
              modalAddress = false;
              areaNameVar = 'home';
            "
            class="bg-red-500 hover:bg-red-600 hover:text-black text-white font-bold text-base py-2 px-4 mx-1 mt-2 rounded"
          >
            Close
          </button>
        </div>
      </div>
      <!--  Footer container -->
      <footer class="bg-neutral-100 text-center text-neutral-600 dark:bg-neutral-600 dark:text-neutral-200 lg:text-left">
        <div class="bg-neutral-200 p-6 text-center dark:bg-neutral-700">
          <span>© 2023 Copyright:Healthy Harves</span>
        </div>
      </footer>
    </div>

    <NotificationGroup group="foo">
      <div class="fixed inset-0 flex items-end justify-end p-6 px-4 py-6 pointer-events-none">
        <div class="w-full max-w-sm">
          <Notification v-slot="{ notifications }" enter="transform ease-out duration-300 transition" enter-from="translate-y-2 opacity-0 sm:translate-y-0 sm:translate-x-4" enter-to="translate-y-0 opacity-100 sm:translate-x-0" leave="transition ease-in duration-500" leave-from="opacity-100" leave-to="opacity-0" move="transition duration-500" move-delay="delay-300">
            <div class="flex w-full max-w-sm mx-auto mt-4 overflow-hidden bg-white rounded-lg shadow-md" v-for="notification in notifications" :key="notification.id">
              <div class="flex items-center justify-center w-12 bg-red-500">
                <svg class="w-6 h-6 text-white fill-current" viewBox="0 0 40 40" xmlns="http://www.w3.org/2000/svg">
                  <path d="M20 3.36667C10.8167 3.36667 3.3667 10.8167 3.3667 20C3.3667 29.1833 10.8167 36.6333 20 36.6333C29.1834 36.6333 36.6334 29.1833 36.6334 20C36.6334 10.8167 29.1834 3.36667 20 3.36667ZM19.1334 33.3333V22.9H13.3334L21.6667 6.66667V17.1H27.25L19.1334 33.3333Z" />
                </svg>
              </div>

              <div class="px-4 py-2 -mx-3">
                <div class="mx-3">
                  <span class="font-semibold text-red-500">{{ notification.title }}</span>
                  <p class="text-sm text-gray-600">{{ notification.text }}</p>
                </div>
              </div>
            </div>
          </Notification>
        </div>
      </div>
    </NotificationGroup>
  </div>
</template>
<script>
import NavbarNotLoggedIn from '@/components/NavbarNotLoggedIn.vue';
import api from '../../boot/axios.js';
import Notifications from 'notiwind';
export default {
  components: { NavbarNotLoggedIn },
  data() {
    return {
      modalAddress: false,
      areaNameVar: 'home',
      dataCart: [],
      total: 0,
      dataAdress: [],
      dataArea: [],
      area: '',
      areaName: '',
      address: '',
      feedback: '',
      subTotal: this.$store.getters.cartTotal,
      id: localStorage.getItem('user'),
      selectedOption1: 'option2',
      selectedOption2: 'option2',
      deliveryCharge: 0,
      dataCart: this.$store.getters.cartItems,

      areaVarr: '',
      district: '',
      thana: '',
      zip: '',
      addressVarr: '',
    };
  },
  created() {
    this.getData();
  },

  methods: {
    Toast() {
      this.$notify(
        {
          group: 'foo',
          title: 'Address Blank',
          text: 'Please Choose or Add an address',
        },
        2000
      ); // 2s
    },
    async getData() {
      if (this.dataCart == '') {
        alert('Cart is Empty');
        this.$router.push({ name: 'LiveStock' });
      }
      if (this.id == undefined) {
        alert('Please Log in');
        this.$router.push({ name: 'login' });
      }

      await api
        .get(`/api/area/`)
        .then((res) => {
          this.dataArea = res.data;
          console.table(this.dataArea);
        })
        .catch((error) => {
          console.log(error);
        });

      await api
        .get(`/api/address_cus/?cusId=${this.id}`)
        .then((res) => {
          this.dataAdress = res.data;
          console.table(this.dataAdress);
        })
        .catch((error) => {
          console.log(error);
        });
      this.total = 0;
      this.dataCart = this.$store.getters.cartItems;
      console.table(this.dataCart);
      for (let i = 0; i < this.dataCart.length; i++) {
        this.total += this.dataCart[i].sellingPrice;
      }
    },
    async DeliveryCheck() {
      for (let i = 0; i < this.dataAdress.length; i++) {
        if (this.dataAdress[i].id == this.address) {
          await api
            .get(`/api/area/${this.dataAdress[i].area}/`)
            .then((res) => {
              this.deliveryCharge = res.data.deliveryCharge;
              this.area = res.data.id;
              this.areaName = res.data.name;
            })
            .catch((error) => {
              console.log(error);
            });
        }
      }
    },
    async Order() {
      if (this.address == '') {
        this.Toast();
        return;
      }
      const a = {
        status: 'Pending',
        isLiveStock: true,
        address: this.address,
        area: this.area,
        cusId: this.id,
        deliveryCharge: this.deliveryCharge,
        deliveryMethod: 'Pick up from farm',
        discount: 0,
        feedback: this.feedback,
        rating: 0,
        paymentMethod: 'Cash on delivery',
        paymentStatus: 'Pending',
        scAmount: 0,
        vatAmount: 0,
        deliverySelected: false,
      };
      console.log(a);
      await api
        .post(`/api/order/`, a)
        .then((res) => {
          console.table(res.data.id);
          for (let i = 0; i < this.dataCart.length; i++) {
            const b = {
              tagId: this.dataCart[i].id,
              price: this.dataCart[i].sellingPrice,
              orderId: res.data.id,
              unit: 1,
            };
            api
              .post(`/api/order_prod/`, b)
              .then((res) => {
                console.log(res.data);
              })
              .catch((error) => {
                console.log(error);
              });

            api
              .put(`/api/prod_tag/${this.dataCart[i].id}/`, { status: 'Booked' })
              .then((res) => {
                console.log(res.data);
              })
              .catch((error) => {
                console.log(error);
              });
          }
        })
        .catch((error) => {
          console.log(error);
        });
      this.$store.commit('clearCart');
      this.$router.push('Order');
    },
    async AddressCreate() {
      console.log(true);
      let c;
      for (let i = 0; i < this.dataArea.length; i++) {
        if (this.areaVarr == this.dataArea[i].id) {
          c = this.dataArea[i].name;
          break;
        }
      }
      let a = {
        type: this.areaNameVar,
        address: this.addressVarr,
        area_name: c,
        district: this.district,
        zip: this.zip,
        thana: this.thana,
        active: false,
        cusId: this.id,
        area: this.areaVarr,
      };

      await api
        .post(`/api/address_cus/`, a)
        .then((res) => {
          console.log(res.data);
          api
            .get(`/api/address_cus/?cusId=${this.id}`)
            .then((ress) => {
              this.dataAdress = ress.data;
              console.table(this.dataAdress);
              this.address = res.data.id;
            })
            .catch((error) => {
              console.log(error);
            });
        })
        .catch((error) => {
          console.log(error);
        });
      this.areaNameVar = 'home';
      this.addressVarr = '';
      this.district = '';
      this.thana = '';
      this.zip = '';
      this.areaVarr = '';
      this.modalAddress = false;
    },
  },
};
</script>
